<template>
  <!-- <v-sheet :height="sheetHeight"> -->

  <!-- image !-->
  <v-sheet align="center" justify="center">
    <v-container>
      <!--h2 !-->
      <v-row align="center" justify="center">
        <v-col>
          <h2 class="text-center display-3 hidden-xs-only">
            <strong>Welcome to E-shop!</strong>
          </h2>
          <h2 class="text-center display-1 hidden-sm-and-up">
            <strong>Welcome to E-shop!</strong>
          </h2>
          <v-col md="10">
            <v-carousel cycle show-arrows-on-hover :height="carouselHeight">
              <v-carousel-item
                v-for="(item, i) in items"
                :key="i"
                width="700px"
              >
                <v-row>
                  <v-img :src="item.src"></v-img>
                </v-row>
              </v-carousel-item>
            </v-carousel>
          </v-col>
        </v-col>
      </v-row>
      <v-row> </v-row>
    </v-container>
  </v-sheet>
</template>

<script>
module.exports = {
  data() {
    return {
      items: [
        {
          src:
            "https://raw.githubusercontent.com/garnett0914/garnett0914.github.io/main/img/carousel/main-switch.png",
        },
        {
          src:
            "https://raw.githubusercontent.com/garnett0914/garnett0914.github.io/main/img/carousel/ps5-family.png",
        },
        {
          src:
            "https://raw.githubusercontent.com/garnett0914/garnett0914.github.io/main/img/carousel/xbox-series-x.jpg",
        },
        {
          src:
            "https://raw.githubusercontent.com/garnett0914/garnett0914.github.io/main/img/carousel/xbox-series-s.jpg",
        },
      ],
    };
  },
  computed: {
    carouselHeight() {
      switch (this.$vuetify.breakpoint.name) {
        case "xs":
          return "300px";
        case "sm":
          return "350px";
        case "md":
          return "400px";
        case "lg":
          return "400px";
        case "xl":
          return "400px";
      }
      return "300px";
    },
  },
};
</script>