<template >
  <v-card outlined w>
    <v-sheet :height="imagesheetHeight">
      <v-img
        :src="product.image"
        width="300px"
        height="auto"
        position="center"
      />
    </v-sheet>
    <v-sheet height="100px">
      <v-card-title>{{ product.name }} </v-card-title>
      <v-card-subtitle>${{ product.price }}</v-card-subtitle>
    </v-sheet>
    <v-card-actions>
      <v-btn @click="addToCart(product)" color="success" outlined>
        <v-icon left small>fa-plus</v-icon>
        Add to Cart
      </v-btn>
    </v-card-actions>
  </v-card>
</template>

<script>
export default {
  props: {
    product: Object,
    addToCart: Function,
    // i: Number
  },
  computed: {
    imagesheetHeight() {
      switch (this.$vuetify.breakpoint.name) {
        case "xs":
          return "300px";
        case "sm":
          return "250px";
        case "md":
          return "250px";
        case "lg":
          return "250px";
        case "xl":
          return "300px";
      }
      return "300px";
    },
  },
};
</script>